.product__area {
  margin-bottom: 75px; }

.product__details {
  padding: 10px;
  text-align: center; }

.product__image {
  overflow: hidden; }
  .product__image:hover img {
    -webkit-transform: scale(1.3);
    -ms-transform: scale(1.3);
        transform: scale(1.3); }
  .product__image img {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
        transform: scale(1);
    -webkit-transition: .3s ease-in-out;
    -o-transition: .3s ease-in-out;
    transition: .3s ease-in-out; }

.product__name {
  font-size: 18px;
  font-weight: bold;
  margin: 10px 0;
  color: #0b6da6; }
  .product__name a:hover {
    text-decoration: none; }

.product__price {
  font-size: 18px;
  font-weight: bold;
  margin: 10px 0;
  color: #929292; }



