@import url("https://fonts.googleapis.com/css?family=Cabin:400,700");
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  font-family: "Cabin", sans-serif;
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

img {
  max-width: 100%;
  height: auto; }

* {
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease; }

.product__image img {
  width: 100%; }

@media (max-width: 1200px) {
  body .nav__menu ul li {
    padding: 15px 20px 14px; } }

@media (max-width: 1024px) {
  body .bag__area {
    margin-right: 50px; }
  body .nav__menu ul li {
    height: auto;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between; }
    body .nav__menu ul li.has-sub-menu i {
      display: block; }
    body .nav__menu ul li.active {
      color: #000000;
      background-color: transparent; }
      body .nav__menu ul li.active i {
        -webkit-transform: rotate(90deg);
            -ms-transform: rotate(90deg);
                transform: rotate(90deg); }
    body .nav__menu ul li > ul li {
      color: #000000;
      padding: 10px 0;
      text-transform: capitalize; }
      body .nav__menu ul li > ul li:first-child {
        padding-top: 20px; }
      body .nav__menu ul li > ul li:hover {
        color: #0b6da6; }
  body .nav__menu > ul {
    -ms-flex-direction: column;
        flex-direction: column;
    text-align: left; }
    body .nav__menu > ul > li {
      border-bottom: 1px solid #dadada; }
    body .nav__menu > ul li > ul {
      display: none;
      position: relative;
      top: 0;
      left: 0;
      padding: 0;
      background-color: transparent; }
      body .nav__menu > ul li > ul.active {
        -ms-flex-direction: column;
            flex-direction: column; } }

@media (max-width: 480px) {
  body .banner__area {
    min-height: 100vh; } }



